import React, { useState } from 'react';
import { Row, Col, Card, Form, Button, Alert, ProgressBar, Tab, Tabs, Table, Badge } from 'react-bootstrap';

function TrustedContactPortal() {
  const [uploadStatus, setUploadStatus] = useState('idle'); // idle, uploading, processing, success, error
  const [verificationResult, setVerificationResult] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  
  // Mock policy execution data
  const [policyExecutions] = useState([
    {
      id: 1,
      platform: 'Gmail',
      accountId: 'john.doe@gmail.com',
      actionType: 'delete',
      status: 'completed',
      executedAt: '2024-01-07 11:00:00',
      response: 'Account deletion request submitted successfully',
      confidence: 0.95
    },
    {
      id: 2,
      platform: 'Facebook',
      accountId: 'facebook.com/johndoe',
      actionType: 'memorialize',
      status: 'in_progress',
      executedAt: '2024-01-07 11:05:00',
      response: 'Memorialization request sent, awaiting platform response',
      confidence: 0.88
    },
    {
      id: 3,
      platform: 'Chase Bank',
      accountId: '****1234',
      actionType: 'lock',
      status: 'pending',
      executedAt: null,
      response: 'Waiting for manual review due to high-value account',
      confidence: null
    }
  ]);

  const [verificationHistory] = useState([
    {
      id: 1,
      fileName: 'death_certificate_john_doe.pdf',
      uploadedAt: '2024-01-07 10:30:00',
      status: 'verified',
      extractedName: 'John Doe',
      dateOfDeath: '2024-01-15',
      confidence: 0.95
    }
  ]);

  const handleFileSelect = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
    setUploadStatus('idle');
    setVerificationResult(null);
  };

  const handleUpload = async (e) => {
    e.preventDefault();
    if (!selectedFile) return;

    setUploadStatus('uploading');
    
    // Simulate file upload and processing
    setTimeout(() => {
      setUploadStatus('processing');
      
      // Simulate Azure AI Vision processing
      setTimeout(() => {
        setUploadStatus('success');
        setVerificationResult({
          extractedName: 'John Doe',
          dateOfDeath: '2024-01-15',
          certificateId: 'DC-2024-001234',
          confidence: 0.95,
          status: 'verified'
        });
      }, 3000);
    }, 1000);
  };

  const getStatusMessage = () => {
    switch (uploadStatus) {
      case 'uploading':
        return 'Uploading death certificate...';
      case 'processing':
        return 'Azure AI Vision is processing the document...';
      case 'success':
        return 'Death certificate verified successfully!';
      case 'error':
        return 'Error processing death certificate. Please try again.';
      default:
        return '';
    }
  };

  return (
    <div>
      <h2 className="mb-4">Trusted Contact Portal</h2>
      <p className="text-muted mb-4">
        Verify death events and monitor the execution of digital legacy policies.
      </p>
      
      <Tabs defaultActiveKey="verification" className="mb-4">
        <Tab eventKey="verification" title="Death Certificate Verification">
          <Row>
            <Col md={8}>
              <Card>
                <Card.Header>
                  <h5>Upload Death Certificate</h5>
                </Card.Header>
                <Card.Body>
                  <Alert variant="warning">
                    <strong>Important:</strong> Only authorized trusted contacts can upload death certificates. 
                    Ensure you have proper authorization before proceeding.
                  </Alert>
                  
                  <Form onSubmit={handleUpload}>
                    <Form.Group className="mb-3">
                      <Form.Label>Upload Death Certificate (PDF or Image)</Form.Label>
                      <Form.Control
                        type="file"
                        accept=".pdf,.jpg,.jpeg,.png"
                        onChange={handleFileSelect}
                        disabled={uploadStatus === 'uploading' || uploadStatus === 'processing'}
                      />
                      <Form.Text className="text-muted">
                        Supported formats: PDF, JPG, PNG. Maximum file size: 10MB.
                        The document will be processed using Azure AI Vision for automatic verification.
                      </Form.Text>
                    </Form.Group>
                    
                    {selectedFile && (
                      <Alert variant="info">
                        Selected file: {selectedFile.name} ({(selectedFile.size / 1024 / 1024).toFixed(2)} MB)
                      </Alert>
                    )}
                    
                    {uploadStatus !== 'idle' && (
                      <div className="mb-3">
                        <p>{getStatusMessage()}</p>
                        {(uploadStatus === 'uploading' || uploadStatus === 'processing') && (
                          <ProgressBar animated now={uploadStatus === 'uploading' ? 30 : 70} />
                        )}
                      </div>
                    )}
                    
                    <Button 
                      type="submit" 
                      variant="primary"
                      disabled={!selectedFile || uploadStatus === 'uploading' || uploadStatus === 'processing'}
                    >
                      {uploadStatus === 'uploading' || uploadStatus === 'processing' ? 'Processing...' : 'Verify Death Certificate'}
                    </Button>
                  </Form>
                </Card.Body>
              </Card>
              
              {/* Verification History */}
              <Card className="mt-4">
                <Card.Header>
                  <h5>Verification History</h5>
                </Card.Header>
                <Card.Body>
                  {verificationHistory.length === 0 ? (
                    <p className="text-muted">No previous verifications.</p>
                  ) : (
                    <Table responsive>
                      <thead>
                        <tr>
                          <th>File Name</th>
                          <th>Upload Date</th>
                          <th>Extracted Name</th>
                          <th>Date of Death</th>
                          <th>Status</th>
                          <th>Confidence</th>
                        </tr>
                      </thead>
                      <tbody>
                        {verificationHistory.map((record) => (
                          <tr key={record.id}>
                            <td>{record.fileName}</td>
                            <td>{record.uploadedAt}</td>
                            <td>{record.extractedName}</td>
                            <td>{record.dateOfDeath}</td>
                            <td>
                              <Badge bg={record.status === 'verified' ? 'success' : 'warning'}>
                                {record.status}
                              </Badge>
                            </td>
                            <td>{(record.confidence * 100).toFixed(1)}%</td>
                          </tr>
                        ))}
                      </tbody>
                    </Table>
                  )}
                </Card.Body>
              </Card>
            </Col>
            
            <Col md={4}>
              {verificationResult && (
                <Card>
                  <Card.Header>
                    <h5>Verification Results</h5>
                  </Card.Header>
                  <Card.Body>
                    <div className="mb-2">
                      <strong>Extracted Name:</strong><br />
                      {verificationResult.extractedName}
                    </div>
                    <div className="mb-2">
                      <strong>Date of Death:</strong><br />
                      {verificationResult.dateOfDeath}
                    </div>
                    <div className="mb-2">
                      <strong>Certificate ID:</strong><br />
                      {verificationResult.certificateId}
                    </div>
                    <div className="mb-2">
                      <strong>Confidence Score:</strong><br />
                      {(verificationResult.confidence * 100).toFixed(1)}%
                    </div>
                    <div className="mb-3">
                      <strong>Status:</strong><br />
                      <span className={`badge bg-${verificationResult.status === 'verified' ? 'success' : 'warning'}`}>
                        {verificationResult.status}
                      </span>
                    </div>
                    
                    {verificationResult.status === 'verified' && (
                      <Alert variant="success">
                        <strong>Verification Complete!</strong><br />
                        Digital assets have been frozen and policies will be executed automatically.
                        Monitor progress in the Policy Execution tab.
                      </Alert>
                    )}
                  </Card.Body>
                </Card>
              )}
              
              <Card className="mt-3">
                <Card.Header>
                  <h5>How It Works</h5>
                </Card.Header>
                <Card.Body>
                  <ol>
                    <li><strong>Upload:</strong> Submit death certificate document</li>
                    <li><strong>AI Processing:</strong> Azure AI Vision extracts key information</li>
                    <li><strong>Verification:</strong> System validates against user records</li>
                    <li><strong>Asset Freeze:</strong> Digital assets are immediately secured</li>
                    <li><strong>Policy Execution:</strong> AI interprets and executes user policies</li>
                  </ol>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Tab>
        
        <Tab eventKey="monitoring" title={`Policy Execution (${policyExecutions.length})`}>
          <Row>
            <Col>
              <Card>
                <Card.Header>
                  <h5>Policy Execution Status</h5>
                </Card.Header>
                <Card.Body>
                  <Alert variant="info">
                    <strong>Automated Execution:</strong> Azure OpenAI is interpreting user policies and generating 
                    platform-specific notifications. Monitor the progress below.
                  </Alert>
                  
                  <Table responsive>
                    <thead>
                      <tr>
                        <th>Platform</th>
                        <th>Account</th>
                        <th>Action</th>
                        <th>Status</th>
                        <th>Executed At</th>
                        <th>AI Confidence</th>
                        <th>Response</th>
                      </tr>
                    </thead>
                    <tbody>
                      {policyExecutions.map((execution) => (
                        <tr key={execution.id}>
                          <td><strong>{execution.platform}</strong></td>
                          <td>{execution.accountId}</td>
                          <td>
                            <Badge bg={
                              execution.actionType === 'delete' ? 'danger' :
                              execution.actionType === 'memorialize' ? 'warning' :
                              execution.actionType === 'transfer' ? 'success' : 'secondary'
                            }>
                              {execution.actionType}
                            </Badge>
                          </td>
                          <td>
                            <Badge bg={
                              execution.status === 'completed' ? 'success' :
                              execution.status === 'in_progress' ? 'primary' :
                              execution.status === 'pending' ? 'warning' : 'danger'
                            }>
                              {execution.status.replace('_', ' ')}
                            </Badge>
                          </td>
                          <td>
                            {execution.executedAt ? (
                              <small>{execution.executedAt}</small>
                            ) : (
                              <span className="text-muted">-</span>
                            )}
                          </td>
                          <td>
                            {execution.confidence ? (
                              `${(execution.confidence * 100).toFixed(1)}%`
                            ) : (
                              <span className="text-muted">-</span>
                            )}
                          </td>
                          <td>
                            <small>{execution.response}</small>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                  
                  <Alert variant="secondary" className="mt-3">
                    <strong>Legend:</strong>
                    <ul className="mb-0 mt-2">
                      <li><Badge bg="success">Completed</Badge> - Action successfully executed</li>
                      <li><Badge bg="primary">In Progress</Badge> - Notification sent, awaiting platform response</li>
                      <li><Badge bg="warning">Pending</Badge> - Waiting for manual review or additional documentation</li>
                      <li><Badge bg="danger">Failed</Badge> - Execution failed, manual intervention required</li>
                    </ul>
                  </Alert>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Tab>
        
        <Tab eventKey="dashboard" title="Status Dashboard">
          <Row>
            <Col md={3}>
              <Card className="text-center">
                <Card.Body>
                  <h3 className="text-success">1</h3>
                  <p className="mb-0">Verified Deaths</p>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center">
                <Card.Body>
                  <h3 className="text-primary">3</h3>
                  <p className="mb-0">Total Policies</p>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center">
                <Card.Body>
                  <h3 className="text-success">1</h3>
                  <p className="mb-0">Completed</p>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center">
                <Card.Body>
                  <h3 className="text-warning">2</h3>
                  <p className="mb-0">In Progress</p>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          
          <Row className="mt-4">
            <Col>
              <Card>
                <Card.Header>
                  <h5>Recent Activity</h5>
                </Card.Header>
                <Card.Body>
                  <div className="timeline">
                    <div className="timeline-item mb-3">
                      <div className="d-flex">
                        <Badge bg="success" className="me-2"></Badge>
                        <div>
                          <strong>Death Certificate Verified</strong><br />
                          <small className="text-muted">2024-01-07 10:30:00 - John Doe certificate processed with 95% confidence</small>
                        </div>
                      </div>
                    </div>
                    
                    <div className="timeline-item mb-3">
                      <div className="d-flex">
                        <Badge bg="warning" className="me-2"></Badge>
                        <div>
                          <strong>Digital Assets Frozen</strong><br />
                          <small className="text-muted">2024-01-07 10:31:00 - All registered assets secured automatically</small>
                        </div>
                      </div>
                    </div>
                    
                    <div className="timeline-item mb-3">
                      <div className="d-flex">
                        <Badge bg="primary" className="me-2"></Badge>
                        <div>
                          <strong>AI Policy Interpretation Started</strong><br />
                          <small className="text-muted">2024-01-07 10:32:00 - Azure OpenAI processing user policies</small>
                        </div>
                      </div>
                    </div>
                    
                    <div className="timeline-item mb-3">
                      <div className="d-flex">
                        <Badge bg="success" className="me-2"></Badge>
                        <div>
                          <strong>Gmail Deletion Request Sent</strong><br />
                          <small className="text-muted">2024-01-07 11:00:00 - Professional notification generated and delivered</small>
                        </div>
                      </div>
                    </div>
                    
                    <div className="timeline-item mb-3">
                      <div className="d-flex">
                        <Badge bg="info" className="me-2"></Badge>
                        <div>
                          <strong>Facebook Memorialization Request Sent</strong><br />
                          <small className="text-muted">2024-01-07 11:05:00 - Awaiting platform response</small>
                        </div>
                      </div>
                    </div>
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Tab>
      </Tabs>
    </div>
  );
}

export default TrustedContactPortal;