import React from 'react';
import { Row, Col, Card, Button } from 'react-bootstrap';

function Dashboard() {
  return (
    <div>
      <h1 className="mb-4">Ghost Identity Protection System</h1>
      <p className="lead text-muted mb-5">
        Secure your digital legacy and protect against posthumous identity misuse
      </p>
      
      <Row>
        <Col md={4}>
          <Card className="dashboard-card">
            <Card.Body>
              <Card.Title>Action Policies</Card.Title>
              <Card.Text>
                Create detailed policies for how your digital assets should be 
                handled after death using natural language instructions.
              </Card.Text>
              <Button variant="primary" href="/policies">
                Manage Policies
              </Button>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={4}>
          <Card className="dashboard-card">
            <Card.Body>
              <Card.Title>Trusted Contacts</Card.Title>
              <Card.Text>
                Designate trusted contacts who can verify death events and 
                execute your digital legacy policies.
              </Card.Text>
              <Button variant="primary" href="/trusted-contact">
                Contact Portal
              </Button>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={4}>
          <Card className="dashboard-card">
            <Card.Body>
              <Card.Title>System Administration</Card.Title>
              <Card.Text>
                Monitor system operations, view audit logs, and track 
                AI-powered policy execution results.
              </Card.Text>
              <Button variant="primary" href="/admin">
                Admin View
              </Button>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <Row className="mt-3">
        <Col md={6}>
          <Card className="dashboard-card">
            <Card.Body>
              <Card.Title>Digital Vault</Card.Title>
              <Card.Text>
                Register and manage your digital assets including email accounts, 
                bank accounts, and social media profiles.
              </Card.Text>
              <Button variant="outline-primary" href="/vault">
                Manage Vault
              </Button>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <Row className="mt-5">
        <Col>
          <Card>
            <Card.Body>
              <Card.Title>How It Works</Card.Title>
              <Row>
                <Col md={3}>
                  <h5>1. Register Assets</h5>
                  <p>Add your digital accounts and assets to the secure vault</p>
                </Col>
                <Col md={3}>
                  <h5>2. Set Policies</h5>
                  <p>Define what should happen to each asset (delete, memorialize, etc.)</p>
                </Col>
                <Col md={3}>
                  <h5>3. Add Contacts</h5>
                  <p>Designate trusted contacts for death verification</p>
                </Col>
                <Col md={3}>
                  <h5>4. AI Execution</h5>
                  <p>Azure AI services automatically execute your policies when needed</p>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default Dashboard;