import React, { useState } from 'react';
import { Row, Col, Card, Form, Button, Table, Badge, Tab, Tabs, Alert } from 'react-bootstrap';

function UserVault() {
  const [assets, setAssets] = useState([]);
  const [trustedContacts, setTrustedContacts] = useState([]);
  const [newAsset, setNewAsset] = useState({
    assetType: 'email',
    platformName: '',
    accountIdentifier: '',
    credentials: '',
    actionType: 'delete'
  });
  const [newContact, setNewContact] = useState({
    name: '',
    email: '',
    phone: '',
    relationship: ''
  });

  const handleAssetSubmit = (e) => {
    e.preventDefault();
    const asset = {
      ...newAsset,
      id: Date.now(),
      status: 'active',
      createdAt: new Date().toISOString()
    };
    setAssets([...assets, asset]);
    setNewAsset({
      assetType: 'email',
      platformName: '',
      accountIdentifier: '',
      credentials: '',
      actionType: 'delete'
    });
  };

  const handleContactSubmit = (e) => {
    e.preventDefault();
    const contact = {
      ...newContact,
      id: Date.now(),
      status: 'pending',
      createdAt: new Date().toISOString()
    };
    setTrustedContacts([...trustedContacts, contact]);
    setNewContact({
      name: '',
      email: '',
      phone: '',
      relationship: ''
    });
  };

  const removeAsset = (id) => {
    setAssets(assets.filter(asset => asset.id !== id));
  };

  const removeContact = (id) => {
    setTrustedContacts(trustedContacts.filter(contact => contact.id !== id));
  };

  return (
    <div>
      <h2 className="mb-4">Digital Asset Vault</h2>
      <p className="text-muted mb-4">
        Securely register your digital assets and manage trusted contacts for your digital legacy.
      </p>
      
      <Tabs defaultActiveKey="assets" className="mb-4">
        <Tab eventKey="assets" title={`Digital Assets (${assets.length})`}>
          <Row>
            <Col md={6}>
              <Card>
                <Card.Header>
                  <h5>Add New Digital Asset</h5>
                </Card.Header>
                <Card.Body>
                  <Form onSubmit={handleAssetSubmit}>
                    <Form.Group className="mb-3">
                      <Form.Label>Asset Type *</Form.Label>
                      <Form.Select 
                        value={newAsset.assetType}
                        onChange={(e) => setNewAsset({...newAsset, assetType: e.target.value})}
                        required
                      >
                        <option value="email">Email Account</option>
                        <option value="bank">Bank Account</option>
                        <option value="social_media">Social Media</option>
                        <option value="other">Other</option>
                      </Form.Select>
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Platform Name *</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="e.g., Gmail, Chase Bank, Facebook"
                        value={newAsset.platformName}
                        onChange={(e) => setNewAsset({...newAsset, platformName: e.target.value})}
                        required
                      />
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Account Identifier *</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="e.g., email address, account number"
                        value={newAsset.accountIdentifier}
                        onChange={(e) => setNewAsset({...newAsset, accountIdentifier: e.target.value})}
                        required
                      />
                      <Form.Text className="text-muted">
                        This information will be encrypted and stored securely.
                      </Form.Text>
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Credentials/Notes</Form.Label>
                      <Form.Control
                        as="textarea"
                        rows={2}
                        placeholder="Additional account details or recovery information"
                        value={newAsset.credentials}
                        onChange={(e) => setNewAsset({...newAsset, credentials: e.target.value})}
                      />
                      <Form.Text className="text-muted">
                        Optional. All sensitive data is encrypted using industry-standard encryption.
                      </Form.Text>
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Default Action Policy *</Form.Label>
                      <Form.Select 
                        value={newAsset.actionType}
                        onChange={(e) => setNewAsset({...newAsset, actionType: e.target.value})}
                        required
                      >
                        <option value="delete">Delete Account</option>
                        <option value="memorialize">Memorialize Account</option>
                        <option value="transfer">Transfer to Contact</option>
                        <option value="lock">Lock Account</option>
                      </Form.Select>
                      <Form.Text className="text-muted">
                        You can create detailed policies in the Action Policies tab.
                      </Form.Text>
                    </Form.Group>
                    
                    <Button type="submit" variant="primary">
                      Add Asset to Vault
                    </Button>
                  </Form>
                </Card.Body>
              </Card>
            </Col>
            
            <Col md={6}>
              <Card>
                <Card.Header>
                  <h5>Registered Assets ({assets.length})</h5>
                </Card.Header>
                <Card.Body style={{maxHeight: '500px', overflowY: 'auto'}}>
                  {assets.length === 0 ? (
                    <Alert variant="info">
                      <strong>No assets registered yet.</strong><br />
                      Start by adding your email accounts, bank accounts, and social media profiles.
                    </Alert>
                  ) : (
                    <Table responsive size="sm">
                      <thead>
                        <tr>
                          <th>Platform</th>
                          <th>Type</th>
                          <th>Action</th>
                          <th>Status</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        {assets.map((asset) => (
                          <tr key={asset.id}>
                            <td>
                              <strong>{asset.platformName}</strong><br />
                              <small className="text-muted">{asset.accountIdentifier}</small>
                            </td>
                            <td>
                              <Badge bg="secondary">
                                {asset.assetType.replace('_', ' ')}
                              </Badge>
                            </td>
                            <td>
                              <Badge bg="info">
                                {asset.actionType}
                              </Badge>
                            </td>
                            <td>
                              <Badge bg="success">
                                {asset.status}
                              </Badge>
                            </td>
                            <td>
                              <Button 
                                variant="outline-danger" 
                                size="sm"
                                onClick={() => removeAsset(asset.id)}
                              >
                                Remove
                              </Button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </Table>
                  )}
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Tab>
        
        <Tab eventKey="contacts" title={`Trusted Contacts (${trustedContacts.length})`}>
          <Row>
            <Col md={6}>
              <Card>
                <Card.Header>
                  <h5>Add Trusted Contact</h5>
                </Card.Header>
                <Card.Body>
                  <Alert variant="warning">
                    <strong>Important:</strong> Trusted contacts can verify death events and execute your digital legacy policies. 
                    Choose people you trust completely.
                  </Alert>
                  
                  <Form onSubmit={handleContactSubmit}>
                    <Form.Group className="mb-3">
                      <Form.Label>Full Name *</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter full legal name"
                        value={newContact.name}
                        onChange={(e) => setNewContact({...newContact, name: e.target.value})}
                        required
                      />
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Email Address *</Form.Label>
                      <Form.Control
                        type="email"
                        placeholder="contact@example.com"
                        value={newContact.email}
                        onChange={(e) => setNewContact({...newContact, email: e.target.value})}
                        required
                      />
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Phone Number</Form.Label>
                      <Form.Control
                        type="tel"
                        placeholder="+1 (555) 123-4567"
                        value={newContact.phone}
                        onChange={(e) => setNewContact({...newContact, phone: e.target.value})}
                      />
                    </Form.Group>
                    
                    <Form.Group className="mb-3">
                      <Form.Label>Relationship *</Form.Label>
                      <Form.Select
                        value={newContact.relationship}
                        onChange={(e) => setNewContact({...newContact, relationship: e.target.value})}
                        required
                      >
                        <option value="">Select relationship</option>
                        <option value="spouse">Spouse</option>
                        <option value="parent">Parent</option>
                        <option value="child">Child</option>
                        <option value="sibling">Sibling</option>
                        <option value="friend">Close Friend</option>
                        <option value="attorney">Attorney</option>
                        <option value="executor">Estate Executor</option>
                        <option value="other">Other</option>
                      </Form.Select>
                    </Form.Group>
                    
                    <Button type="submit" variant="primary">
                      Add Trusted Contact
                    </Button>
                  </Form>
                </Card.Body>
              </Card>
            </Col>
            
            <Col md={6}>
              <Card>
                <Card.Header>
                  <h5>Trusted Contacts ({trustedContacts.length})</h5>
                </Card.Header>
                <Card.Body style={{maxHeight: '500px', overflowY: 'auto'}}>
                  {trustedContacts.length === 0 ? (
                    <Alert variant="info">
                      <strong>No trusted contacts added yet.</strong><br />
                      Add at least one trusted contact who can verify death events and execute your policies.
                    </Alert>
                  ) : (
                    <Table responsive size="sm">
                      <thead>
                        <tr>
                          <th>Name</th>
                          <th>Contact Info</th>
                          <th>Relationship</th>
                          <th>Status</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        {trustedContacts.map((contact) => (
                          <tr key={contact.id}>
                            <td>
                              <strong>{contact.name}</strong>
                            </td>
                            <td>
                              <small>
                                {contact.email}<br />
                                {contact.phone && contact.phone}
                              </small>
                            </td>
                            <td>
                              <Badge bg="secondary">
                                {contact.relationship}
                              </Badge>
                            </td>
                            <td>
                              <Badge bg={contact.status === 'verified' ? 'success' : 'warning'}>
                                {contact.status}
                              </Badge>
                            </td>
                            <td>
                              <Button 
                                variant="outline-danger" 
                                size="sm"
                                onClick={() => removeContact(contact.id)}
                              >
                                Remove
                              </Button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </Table>
                  )}
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Tab>
      </Tabs>
    </div>
  );
}

export default UserVault;